import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ViewsComponent } from '../views.component';
import { TutorialsExampleComponent } from './tutorials-examples.component';
import { TutorialsApiComponent } from './tutorials-api.component';
import { NwOverviewSectionComponent } from 'src/app/shared/components/overview';
import { FakeApiOverviewComponent } from './fake-api/overview/fake-api-overview.component';
const OVERVIEW = {
    'inline-usage': 'Declarative usage',
    'toast-service': 'Building a toast management service'
};

export const TUTORIALS_VIEWS_ROUTES: Routes = [
    {
        path: 'tutorials',
        pathMatch: 'full',
        redirectTo: 'tutorials/fake-api',
        data: {
            name: 'tutorials',
            title: 'Cẩm nang'
        }
    },
    {
        path: 'tutorials/fake-api',
        data: {
            name: 'Fake API',
            title: 'Giả tạo API'
        },
        children: [
            {
                path: '', pathMatch: 'full', redirectTo: 'examples'
            },
            {
                path: '',
                component: ViewsComponent,
                data: { OVERVIEW },
                children: [
                    {
                        path: 'overview',
                        component: FakeApiOverviewComponent
                    },
                    {
                        path: 'examples',
                        component: TutorialsExampleComponent
                    },
                    {
                        path: 'api',
                        component: TutorialsApiComponent
                    }
                ]
            }
        ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(TUTORIALS_VIEWS_ROUTES)],
    exports: [RouterModule]
})
export class TutorialsRoutingModule { }
