import { Component } from '@angular/core';
import { Snippet } from 'src/app/shared/components/code/code.component';
import { NwOverview } from 'src/app/shared/components/overview';
import { PostPartialService } from 'src/app/core/services/post-partial.service';

@Component({
  selector: 'app-fake-api-overview',
  templateUrl: './fake-api-overview.component.html',
  host: { '[class.overview]': 'true' }
})
export class FakeApiOverviewComponent {
  TOAST_INLINE_BASIC = Snippet({
    lang: 'html',
    code: `
      <nw-toast header="Notification header">
        Content of the notification
      </nw-toast>`
  });

  TOAST_INLINE_LIFECYCLE = Snippet({
    lang: 'html',
    code: `
      <!-- Using *ngIf to toggle display (showToast = true initially) -->
      <nw-toast header="I can be closed!" *ngIf="showToast" (hide)="showToast = false">
        <!-- Content here -->
      </nw-toast>

      <!-- or looping over a collection of toasts with *ngFor -->
      <nw-toast [header]="'Toast #'+index+' here!'"
        *ngFor="let toast of toasts; index as index" (hide)="toasts.splice(index, 1)">
        <!-- Content here -->
      </nw-toast>`,
  });

  APP_TOAST_SERVICE = Snippet({
    lang: 'typescript',
    code: `
      @Injectable({ providedIn: 'root' })
      export class AppToastService {
        toasts: any[] = [];

        show(header: string, body: string) {
          this.toasts.push({ header, body });
        }
      }`,
  });

  APP_TOAST_SERVICE_REMOVE = Snippet({
    lang: 'typescript',
    code: `
      remove(toast) {
        this.toasts = this.toasts.filter(t => t != toast);
      }`,
  });

  APP_TOASTS_CONTAINER_TPL = Snippet({
    lang: 'html',
    code: `
      <nw-toast
        *ngFor="let toast of toastService.toasts"
        [header]="toast.header" [autohide]="true" [delay]="toast.delay || 5000"
        (hide)="toastService.remove(toast)"
      >{{toast.body}}</nw-toast>`,
  });

  APP_TOASTS_CONTAINER_STYLES = Snippet({
    lang: 'css',
    code: `
      :host {
        position: fixed;
        top: 0;
        right: 0;
        margin: 0.5em;
        z-index: 1200;
      }`,
  });

  APP_TOASTS_CONTAINER = Snippet({
    lang: 'typescript',
    code: `
      @Component({
        selector: 'app-toasts',
        template: ' ... ',
        styles: [' ... ']
      })
      export class AppToastsComponent {
        constructor(toastService: AppToastService) {}
      }`,
  });

  CONTAINER_USAGE = Snippet({
    lang: 'html',
    code: `
      <!-- somewhere in your root component template -->
      <app-toasts aria-live="polite" aria-atomic="true"></app-toasts>`,
  });

  sections: NwOverview = {};

  constructor(partialService: PostPartialService) { this.sections = partialService.getOverviewSections('fake-api'); }
}
