import { CommonModule } from '@angular/common';
import { NgModule, NO_ERRORS_SCHEMA } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { NwWidgetModule } from '@nw-widget/nw-widget';
import { CodeComponent } from './components/code/code.component';
import { WidgetPartialComponent } from './components/widget-partial/widget-partial.component';
import { FragmentDirective } from './directives/fragment.directive';
import { NwApiDocs, NwApiDocsConfig, NwApiDocsClass, NwApiDocsBadge } from './components/api-docs';
import { NwOverviewDirective, NwOverviewSectionComponent } from './components/overview';

const FLY_DECLARATION = [CodeComponent, WidgetPartialComponent, NwOverviewSectionComponent, NwApiDocs, NwApiDocsConfig, NwApiDocsClass, NwApiDocsBadge, NwOverviewDirective, FragmentDirective];

@NgModule({
  declarations: [
    ...FLY_DECLARATION
  ],
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    NwWidgetModule
  ],
  exports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    NwWidgetModule,
    ...FLY_DECLARATION
  ],
  schemas: [NO_ERRORS_SCHEMA]
})
export class SharedModule { }
