import { Component, Input, ChangeDetectionStrategy } from '@angular/core';
import { Snippet, ISnippet } from '../code/code.component';
import { AnalyticService } from 'src/app/core/services/analytic.service';

@Component({
  selector: 'app-widget-partial',
  templateUrl: './widget-partial.component.html',
  styleUrls: ['./widget-partial.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class WidgetPartialComponent {
  @Input() partialTitle: string;
  @Input() component: string;
  @Input() id: string;
  @Input() code: string;
  @Input() stylesheet: ISnippet;
  @Input() markup: string;
  @Input() files: { name: string; source: string }[];
  @Input() showCode = false;

  get markupSnippet() { return Snippet({ lang: 'html', code: this.markup }); }
  get stylesheetSnippet() { return Snippet(this.stylesheet); }
  get codeSnippet() { return Snippet({ lang: 'typescript', code: this.code }); }

  getFileSnippet({ name, source }) {
    return Snippet({ code: source, lang: name.split('.').pop() });
  }

  get hasManyFiles() {
    return this.files && this.files.length > 5;
  }

  constructor(
    private _analytics: AnalyticService
  ) { }

  tabType(name: string) {
    const ext = name.split('.').pop();
    return (
      {
        html: 'HTML',
        scss: 'Style (SCSS)',
        css: 'Style (CSS)',
        ts: 'Typescript'
      }[ext] || 'Code'
    );
  }

  trackStackBlitzClick() {
    this._analytics.trackEvent(
      'StackBlitz View',
      this.component + ' ' + this.id
    );
  }
  trackShowCodeClick() {
    if (this.showCode) {
      this._analytics.trackEvent(
        'Show Code View',
        this.component + ' ' + this.id
      );
    }
  }

}
