import { Directive, AfterContentChecked, Input, ContentChildren, QueryList } from '@angular/core';
import { NwTabContent } from './nw-tab-content.directive';
import { NwTabTitle } from './nw-tab-title.directive';
let nwTabNextId = 0;
@Directive({
  selector: 'nw-tab',
  exportAs: 'nwTab'
})
export class NwTab implements AfterContentChecked {
  /**
   * The tab identifier.
   *
   * Must be unique for the entire document for proper accessibility support.
   */
  @Input() id = `ngb-tab-${nwTabNextId++}`;

  /**
   * The tab title.
   *
   * Use the [`NgbTabTitle`](#/components/tabset/api#NgbTabTitle) directive for non-string titles.
   */
  @Input() title: string;

  /**
   * If `true`, the current tab is disabled and can't be toggled.
   */
  @Input() disabled = false;

  titleTpl: NwTabTitle | null;
  contentTpl: NwTabContent | null;

  @ContentChildren(NwTabTitle, {descendants: false}) titleTpls: QueryList<NwTabTitle>;
  @ContentChildren(NwTabContent, {descendants: false}) contentTpls: QueryList<NwTabContent>;

  ngAfterContentChecked() {
    // We are using @ContentChildren instead of @ContentChild as in the Angular version being used
    // only @ContentChildren allows us to specify the {descendants: false} option.
    // Without {descendants: false} we are hitting bugs described in:
    // https://github.com/ng-bootstrap/ng-bootstrap/issues/2240
    this.titleTpl = this.titleTpls.first;
    this.contentTpl = this.contentTpls.first;
  }

}
