import { Injectable } from '@angular/core';

export interface IPartialConfig {
  title: string;
  code?: string;
  markup?: string;
  type: any;
  files?: Array<{ [name: string]: string }>;
  showCode?: boolean;
}

export interface IPartialCollectionConfig { [partial: string]: IPartialConfig; }

export interface IPartialOverviewConfig { [anchor: string]: string; }

@Injectable({
  providedIn: 'root'
})
export class PostPartialService {

  private _partials: { [widget: string]: IPartialCollectionConfig } = {};

  private _overviews: { [widget: string]: IPartialOverviewConfig } = {};


  register(widget: string, partialArguments: IPartialCollectionConfig, overview?: IPartialOverviewConfig) {
    this._partials[widget] = partialArguments;
    if (overview) {
      this._overviews[widget] = overview;
    }
  }

  getPartials(widget: string) { return this._partials[widget]; }

  getOverviewSections(widget: string) {
    const overview = this._overviews[widget];
    const sections = {};
    if (overview) {
      Object.keys(overview).forEach(fragment => { sections[fragment] = { fragment, title: overview[fragment] }; });
    }
    return sections;
  }
}
