import * as moment from 'moment';
export interface IPerson {
    id?: string;
    name?: string;
    birthday?: moment.Moment;
    gender?: string;
    email?: string;
    phone?: string;
    address?: string;
}

export class Person implements IPerson {
    constructor(
        public id?: string,
        public name?: string,
        public birthday?: moment.Moment,
        public gender?: string,
        public email?: string,
        public phone?: string,
        public address?: string
    ) { }
}