import { ChangeDetectionStrategy, Component, Input } from '@angular/core';

import { NwOverviewSection } from './overview';

@Component({
  selector: 'nw-overview-section',
  changeDetection: ChangeDetectionStrategy.OnPush,
  host: {
    'class': 'd-block'
  },
  template: `
    <h2>
      <a class="title-fragment" [routerLink]="" [fragment]="section.fragment" nwFragment>
        <img src="assets/images/link-symbol.svg" />
      </a>
      {{ section.title }}
    </h2>

    <ng-content></ng-content>
  `
})
export class NwOverviewSectionComponent {
  @Input() section: NwOverviewSection;
}
